@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;1,300;1,800&family=Nunito+Sans:wght@200;400&family=Roboto:wght@100;300;400&display=swap');

*{
	font-family: 'Roboto', sans-serif;
	font-size: 1.2rem;
	color: #7D71D8;
	padding: 0;
	margin: 0;
	border: 0;
}
*,*:before,*:after{
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}

nav,footer,header,aside{display: block;}

html,body{
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
input,button,textarea{font-family:inherit;}

input::-ms-clear{display: none;}
button{cursor: pointer; transition: all 0.2s linear 0s;}
button:hover{background-color: rgb(69, 69, 251); transition: all 0.2s linear 0s;}
button::-moz-focus-inner {padding:0;border:0;}
a {color: #7D71D8; text-decoration: none;}
a, a:visited{text-decoration: none;}
a:hover{text-decoration: none; color: #9c91eb}
ul li{list-style: none;}
img{vertical-align: top;}

h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight: 400;}
